.content{
    display: flex;
    justify-content: space-around;
}

.settings{
    width: 45%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 300;
}

.settings > span{
    font-size: 30;
}


@media (max-width: 800px){
    .content{
        flex-direction: column-reverse;
        align-items: center;
    }

    .settings{
        width: 100%;
    }
}

.settings_select{
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 100%;
    flex: 0.8;
    justify-content: space-evenly;
}